<template>
  <BlockSection width="248px" height="250px" title="单据推送数量" theme="2">
    <div class="section__body row" style="padding: 0 10px">
      <div class="chart flex-main">
        <div class="outer-chart">
          <div ref="chart" class="inner-chart"></div>
        </div>
      </div>

      <DataIncFlash
        :data="data.total"
        :flash-style="{
          top: '-140px',
          left: '100px',
          fontSize: '20px',
        }"
      />
    </div>
  </BlockSection>
</template>

<script>
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("bizFinBillPush")],

  data() {
    return {};
  },

  computed: {
    chartOptions() {
      return {
        color: ["#a034d5", "#ae1937", "#c06e0e", "#c7ae17", "#1b8fad"],
        title: {
          text: this.data.total || "--", //主标题文本
          left: "32%",
          top: "42%",
          textStyle: {
            fontSize: 20,
            color: "#fedb65",
            fontWeight: "bold",
            align: "center",
          },
        },
        series: [
          {
            type: "pie",
            radius: [35, 60],
            center: ["50%", "50%"],
            itemStyle: { borderWidth: 1, borderColor: "#000000" },
            left: "0%",
            right: "10%",
            top: 0,
            bottom: 0,
            label: {
              alignTo: "edge",
              formatter: "{name|{b}}\n{percent|{d}%}",
              minMargin: 5,
              edgeDistance: 0,
              lineHeight: 20,
              rich: {
                name: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#fff",
                  width: 0,
                },
                percent: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#ffff00",
                  width: 0,
                },
                value: { fontSize: 14, fontWeight: "bold", color: "#33ccff" },
              },
            },
            labelLine: {
              length: 10,
              length2: 10,
            },
            labelLayout: (params) => {
              const isLeft = params.labelRect.x < this.chart.getWidth() / 2;
              const points = params.labelLinePoints;
              points[2][0] = isLeft
                ? params.labelRect.x
                : params.labelRect.x + params.labelRect.width;
              return {
                labelLinePoints: points,
              };
            },
            data: this.data.list || [],
          },
        ],
      };
    },
  },
};
</script>

<style lang="less" scoped>
.chart {
  .inner-chart {
    width: 240px;
    height: 200px;
  }
}
</style>
