      const valueData1 = data.map((it) => it.yestoday);
      const valueData2 = data.map((it) => it.today);

      return {
        tooltip: {
          show: true,
        },
        grid: {
          left: 0,
          right: 30,
          top: 10,
          bottom: "3%",
          containLabel: true,
