        xAxis: {
          boundaryGap: false,
          axisLabel: {
            color: "#ffffff",
          },
          splitLine: {
            show: true,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
          axisLine: {
            show: true,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
          data: nameData,
        },
        yAxis: {
          axisLabel: {
            color: "#ffffff",
          },
          axisLine: {
            show: true,
            lineStyle: { color: "#5376c3" },
          },
          splitLine: {
            show: false,
            interval: 100,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
        },
        series: [
          {
            type: "line",
            barWidth: 18,
            data: valueData1,
            areaStyle: {
              color: new graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: "rgba(55, 235, 225, 0.5)" },
                { offset: 1, color: "rgba(208, 63, 104, 0.5)" },
              ]),
            },
            itemStyle: {
              color: "#cf489e",
            },
          },
          {
            type: "line",
            barWidth: 18,
            data: valueData2,
            areaStyle: {
              color: new graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: "rgba(55, 235, 225, 0.5)" },
                { offset: 1, color: "rgba(19, 162, 249, 0.5)" },
              ]),
            },
            itemStyle: {
              color: "#13a2f9",
            },
          },
        ],
      };
    },
  },
};
</script>

